let bitArray = code.split(' ');

const CHAR_RECORD_LENGTH = 32;

const offsets = {
    name: 2,
    hp: 14,
    str: 14,
    int: 15,
    dex: 16,
    exp: 22,
    gold: 516,
    keys: 518,
    skullKeys: 523,
    gems: 519,
    blackBadge: 536,
    magicCarpets: 522,
    magicAxes: 576
};

const generateHackedBinary = (data) => {
    const { characters, items } = data;
    characters.map((character, idx) => {
        if (character) {
            let characterOffset = CHAR_RECORD_LENGTH * idx;

            // Name
            if (character.name) {
                let nameOffset = offsets.name + characterOffset;
                let name = strToHex(character.name);
                name.map((bit, index) => {
                    bitArray[nameOffset + index] = bit;
                });
            }

            if (character.str) {
                // STR
                let strOffset = offsets.str + characterOffset;
                let strBit = intToHex(Number(character.str));
                bitArray[strOffset] = strBit;
            }

            if (character.int) {
                // INT
                let intOffset = offsets.int + characterOffset;
                let intBit = intToHex(Number(character.int));
                bitArray[intOffset] = intBit;
            }

            if (character.dex) {
                // DEX
                let dexOffset = offsets.dex + characterOffset;
                let dexBit = intToHex(Number(character.dex));
                bitArray[dexOffset] = dexBit;
            }

            if (character.hp) {
                // HP
                let hpOffset = offsets.hp + characterOffset;
                let hpBit = intToHex(Number(character.hp));
                while (hpBit.length < 4) {
                    hpBit = "0" + hpBit;
                }
                hpBit = hpBit.match(/.{1,2}/g);
                hpBit = [...hpBit, ...hpBit]; // Handle for initial hp and Max HP
                hpBit.map((bit, index) => {
                    bitArray[hpOffset + index] = bit;
                });
            }

            if (character.exp) {
                let expOffset = offsets.exp + characterOffset;
                let expBits = intToHex(Number(character.exp));
                while (expBits.length < 4) {
                    expBits = "0" + expBits;
                }
                expBits = expBits.match(/.{1,2}/g);
                expBits.map((bit, index) => {
                    bitArray[expOffset + index] = bit;
                });
            }

            if (character.gold) {
                let goldOffset = offsets.gold + characterOffset;
                let goldBits = intToHex(Number(character.gold));
                while (goldBits.length < 4) {
                    goldBits = "0" + goldBits;
                }
                goldBits = goldBits.match(/.{1,2}/g);
                goldBits.map((bit, index) => {
                    bitArray[goldOffset + index] = bit;
                });
            }
        }
    });

    for (let item in items) {
        if (items.hasOwnProperty(item)) {
            let itemOffset = offsets[item];
            let itemBits = "63";
            bitArray[itemOffset] = itemBits;
        }
    }

    bitArray = bitArray.join('');

    bitArray = bitArray.match(/.{2}/g).map((val) => parseInt(val, 16));

    var binArray = new Uint8Array(bitArray);

    const blob = new Blob([binArray], { type: 'application/octet-stream' });
    FileSaver.saveAs(blob, "SAVED.GAM");
};

const intToHex = (int) => {
    return int.toString(16);
};

const hexToInt = (int) => {
    return parseInt(int, 16);
};

const strToHex = (str) => {
    let hexArr = [];
    str.split('').map((letter, idx) => {
        let hex = str.charCodeAt(idx).toString(16);
        hexArr.push(hex);
    });
    return hexArr;
};

export default generateHackedBinary;
